html {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 10px;
  background: #000;
}

#canvas {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

#canvas,
#canvas a,
#canvas a:visited,
#canvas a:active {
  font-family: monospace;
  font-size: 20pt;
  color: #afa;
  text-decoration: none;
}
#canvas a:hover {
  text-decoration: underline;
}
.cursor-space {
  position: relative;
}
.cursor {
  position: absolute;
  right: -10px;
  top: 0px;
}
